<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div style="height: 100%; width: 100%" align="center" v-if="!small">
        <v-progress-circular
                v-if="!ideUrl"
                style="margin-top: 25%; color: #1976d2 !important"
                :size="130"
                color="primary"
                indeterminate
        >
            <a>약 2분 정도 <br> 소요됩니다.</a>
        </v-progress-circular>
        <vue-friendly-iframe
                v-else
                :src="ideUrl"
                style="height: 100%; width: 100%"
                frameborder="0" gesture="media" allow="encrypted-media"></vue-friendly-iframe>
    </div>
    <div v-else>
        <v-subheader>
            Logs
        </v-subheader>
        <log-viewer v-model="value.result"></log-viewer>
        <v-divider></v-divider>
    </div>

</template>

<script>

    import LabBase from "../LabBase"
    import LogViewer from '../LogViewer'
    import json2yaml from 'json2yaml'

    export default {
        name: 'lab-tool-jupyter',
        components: {
            LogViewer
        },
        mixins: [LabBase],
        props: {
            value: Object,
            labInfo: Object,
            small: Boolean
        },

        data() {
            return {
                ideInterval: '',
                ideUrl: null, //refactor reason #NIM, #VIFWN
                interval: null, //refactor reason #NIM, #VIFWN
                userId: this.$route.params.userId,
                oldResult: null,
            }
        },

        created: async function () {

            var me = this;
            me.userId = me.$route.params.userId;

            if (!me.small) {
                var hashName = await me.startIDE();
                this.ideInterval = setInterval(function () {
                    me.ideRunningCheck(hashName);
                    //me.loading = false  // refactor reason #NIM, #VIFWN
                }, 3000)
            }
            //TODO: 아래 API 가 안먹히니 천상 polling 을 직접 처리해야 할듯..
            me.interval = setInterval(async function () {
                try {
                    var log = await me.getString(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/result.log`)
                    me.onResultChange(log)
                } catch (e) {

                }
            }, 3000)
        },

        mounted: function () {

        },
        beforeDestroy() {
            clearInterval(this.interval)
        },
        methods: {
            existNamespaceCheck(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.84.88.143/api/v1/namespaces/${hashName}`).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            createNamespace(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.post(`http://34.84.88.143/api/v1/namespaces`, {
                        "apiVersion": "v1",
                        "kind": "Namespace",
                        "metadata": {
                            "name": hashName
                        },
                        "spec": {
                            "finalizers": [
                                "kubernetes"
                            ]
                        }
                    }).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            existServiceAccountCheck(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.84.88.143/api/v1/namespaces/${hashName}/serviceaccounts/${hashName}`).then(function (result) {
                        resolve(result)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            createServiceAccount(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.post(`http://34.84.88.143/api/v1/namespaces/${hashName}/serviceaccounts`, {
                        "apiVersion": "v1",
                        "kind": "ServiceAccount",
                        "metadata": {
                            "name": hashName,
                            "namespace": hashName
                        }
                    }).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            existRoleCheck(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.84.88.143/apis/rbac.authorization.k8s.io/v1beta1/namespaces/${hashName}/roles/${hashName}`).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            createRole(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.post(`http://34.84.88.143/apis/rbac.authorization.k8s.io/v1beta1/namespaces/${hashName}/roles`, {
                        "kind": "Role",
                        "apiVersion": "rbac.authorization.k8s.io/v1beta1",
                        "metadata": {
                            "namespace": hashName,
                            "name": hashName
                        },
                        "rules": [
                            {
                                "apiGroups": [
                                    "*"
                                ],
                                "resources": [
                                    "*"
                                ],
                                "verbs": [
                                    "*"
                                ]
                            }
                        ]
                    }).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            existRoleBindingCheck(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.84.88.143/apis/rbac.authorization.k8s.io/v1beta1/namespaces/${hashName}/rolebindings/${hashName}`).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            createRoleBinding(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.post(`http://34.84.88.143/apis/rbac.authorization.k8s.io/v1beta1/namespaces/${hashName}/rolebindings`, {
                        "kind": "RoleBinding",
                        "apiVersion": "rbac.authorization.k8s.io/v1beta1",
                        "metadata": {
                            "name": hashName,
                            "namespace": hashName
                        },
                        "subjects": [
                            {
                                "kind": "ServiceAccount",
                                "name": hashName
                            }
                        ],
                        "roleRef": {
                            "kind": "Role",
                            "name": hashName,
                            "apiGroup": "rbac.authorization.k8s.io"
                        }
                    }).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            getSecret(hashName, secretName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.84.88.143/api/v1/namespaces/${hashName}/secrets/${secretName}`).then(function (result) {
                        resolve(result.data.data.token)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            onResultChange(change) {
                if (this.oldResult != change) {
                    this.value.result = change;
                    this.oldResult = change;
                    this.$emit('input', this.value);
                    this.$emit('change', this.value);
                }

            },
            hashCode(s) {
                return s.split("").reduce(function (a, b) {
                    a = ((a << 5) - a) + b.charCodeAt(0);
                    return a & a
                }, 0);
            },
            ideRunningCheck(hashName) {
                var me = this
                me.$http.get(`http://34.84.88.143/api/v1/namespaces/default/pods/${hashName}/status`).then(function (result) {
                    if (result.data.status.conditions) {
                        result.data.status.conditions.forEach(function (status) {
                            if (status.type == "Ready" && status.status == "True") {
                                window.open(`http://${hashName}.kuberman.io/`, "_blank")
                                clearInterval(me.ideInterval);
                            }
                        })
                    }
                }).catch(function (e) {
                })
            },
            ideExistCheck(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.84.88.143/api/v1/namespaces/default/pods/${hashName}/status`).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            startIDE() {
                var me = this
                return new Promise(async function (resolve) {
                    var course = me.courseId;
                    var clazz = me.classId;
                    var lab = me.labId;
                    var userEmail = me.userId;
                    var filePath = course + '/runningClass/' + clazz + '/labs/' + lab + '/' + userEmail;
                    var configPath = course + '/runningClass/' + clazz + '/userConfig/' + userEmail;
                    var hashName = "jpt-" + me.hashCode(filePath);
                    var templateFile = me.labInfo.templateFile;
                    var templateFilePath = `/home/project/${course}/planed/${lab}/${templateFile}`.replace('/home/project/', '')
                    var templateFileUrl = await me.minio_presignedURL(templateFilePath);
                    var configUrl = await me.minio_presignedURL(configPath + '/config'.replace('/home/project/', ''));
                    // Config 파일 확인
                    var checkConfigFile = await me.getObject(configPath + '/config');
                    console.log(checkConfigFile)
                    // 만약 Config가 없다면 만들어줌.
                    if (!checkConfigFile) {
                        // 1. Namespace Check
                        var namespaceChecked = await me.existNamespaceCheck(hashName);
                        if (!namespaceChecked) {
                            await me.createNamespace(hashName)
                        }
                        // 2. ServiceAccount Check
                        var serviceAccountChecked = await me.existServiceAccountCheck(hashName);
                        if (!serviceAccountChecked) {
                            await me.createServiceAccount(hashName);
                        }

                        // 3. Role check
                        var roleChecked = await me.existRoleCheck(hashName);
                        if (!roleChecked) {
                            await me.createRole(hashName)
                        }
                        // 4. Role Binding Check
                        var roleBindingChecked = await me.existRoleBindingCheck(hashName);
                        if (!roleBindingChecked) {
                            await me.createRoleBinding(hashName)
                        }
                        // 5. get secret
                        var serviceAccount = await me.existServiceAccountCheck(hashName);
                        var secretName = serviceAccount.data.secrets[0].name
                        var secret = await me.getSecret(hashName, secretName)
                        var decodedToken = atob(secret);

                        // 6. upload Config
                        var configJson = {
                            "apiVersion": "v1",
                            "clusters": [
                                {
                                    "cluster": {
                                        "insecure-skip-tls-verify": true,
                                        "server": "https://34.71.80.144"
                                    },
                                    "name": "kcb-test2.k8s.local"
                                }
                            ],
                            "contexts": [
                                {
                                    "context": {
                                        "cluster": "kcb-test2.k8s.local",
                                        "namespace": hashName,
                                        "user": hashName
                                    },
                                    "name": "kcb-test2.k8s.local"
                                }
                            ],
                            "current-context": "kcb-test2.k8s.local",
                            "kind": "Config",
                            "preferences": {},
                            "users": [
                                {
                                    "name": hashName,
                                    "user": {
                                        "token": decodedToken
                                    }
                                }
                            ]
                        }
                        await me.putObject(configPath + '/config', configJson)
                    }
                    console.log(filePath, templateFile)

                    var ideExistChecked = await me.ideExistCheck(hashName);
                    if (ideExistChecked) {
                        resolve(hashName)
                    }
                    if (me.labInfo.templateFile) {
                        me.$http.post(`http://34.84.88.143/api/v1/namespaces/default/pods`, {
                            "apiVersion": "v1",
                            "kind": "Pod",
                            "metadata": {
                                "name": `${hashName}`,
                                "labels": {
                                    "environment": "jpt",
                                    "userId": me.userId.replace('@', '_'),
                                    "app": `${hashName}`
                                }
                            },
                            "spec": {
                                "initContainers": [
                                    {
                                        "name": "downloadconfig",
                                        "image": "yauritux/busybox-curl",
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/root/.kube",
                                                "name": "test-storage",
                                                "subPath": `labs/${configPath}`
                                            }
                                        ],
                                        "command": ["curl", "-L", configUrl, '--create-dirs', '-o', '/root/.kube/config']
                                    },
                                    {
                                        "name": "mkdir",
                                        "image": "yauritux/busybox-curl",
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/jovyan/work",
                                                "name": "test-storage",
                                                "subPath": `labs`
                                            }
                                        ],
                                        "command": ["mkdir", "-p", `/home/jovyan/work/${filePath}`],
                                    },
                                    {
                                        "name": "downloadtemplate",
                                        "image": "yauritux/busybox-curl",
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/jovyan/work",
                                                "name": "test-storage",
                                                "subPath": `labs/${filePath}`
                                            }
                                        ],
                                        "command": ["curl", "-L", templateFileUrl, '--create-dirs', '-o', '/home/jovyan/work/' + templateFile]
                                    }
                                ],
                                "containers": [
                                    {
                                        "name": `${hashName}`,
                                        "image": "sanghoon01/minimal-notebook:latest",
                                        "securityContext": {
                                            "privileged": true,
                                            "runAsUser": 0
                                        },
                                        "port": [
                                            {"containerPort": 8888}
                                        ],
                                        "command": [
                                            "start-notebook.sh"
                                        ],
                                        "args": ["--NotebookApp.token=''"],
                                        "readinessProbe": {
                                            "httpGet": {
                                                "path": "/",
                                                "port": 8888,
                                                "initialDelaySeconds": 10,
                                                "periodSeconds": 5
                                            }
                                        },
                                        "env": [
                                            {
                                                "name": "COURSE",
                                                "value": `${course}`
                                            },
                                            {
                                                "name": "LABS",
                                                "value": `${lab}`
                                            },
                                            {
                                                "name": "CLAZZ",
                                                "value": `${clazz}`
                                            },
                                            {
                                                "name": "EMAIL",
                                                "value": `${userEmail}`
                                            },

                                        ],
                                        "resources": {
                                            "requests": {
                                                "memory": "2Gi"
                                            }
                                        },
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/jovyan/work",
                                                "name": "test-storage",
                                                "subPath": `labs/${filePath}`
                                            }
                                        ]
                                    }
                                ],
                                "volumes": [
                                    {
                                        "name": "test-storage",
                                        "persistentVolumeClaim": {
                                            "claimName": "nfs"
                                        }
                                    }
                                ]
                            }
                        }).then(function () {
                            me.$http.post(`http://34.84.88.143/api/v1/namespaces/default/services`, {
                                "apiVersion": "v1",
                                "kind": "Service",
                                "metadata": {
                                    "labels": {
                                        "environment": "labs",
                                        "app": `${hashName}`,
                                        "userId": me.userId.replace('@', '_')
                                    },
                                    "name": `${hashName}`,
                                    "namespace": "default"
                                },
                                "spec": {
                                    "ports": [
                                        {
                                            "port": 3000,
                                            "protocol": "TCP",
                                            "targetPort": 8888
                                        }
                                    ],
                                    "selector": {
                                        "app": `${hashName}`
                                    },
                                    "sessionAffinity": "None",
                                    "type": "ClusterIP"
                                },
                                "status": {
                                    "loadBalancer": {}
                                }
                            }).then(
                                function () {
                                    var metaData = {
                                        email: userEmail,
                                        status: "start",
                                        startTime: new Date(),
                                        result: false
                                    }
                                    me.$minioClient.putObject('labs', `${filePath}/User_Lab_Metadata.json`, JSON.stringify(metaData))
                                    resolve(hashName)
                                }
                            )
                        })
                    } else {
                        me.$http.post(`http://34.84.88.143/api/v1/namespaces/default/pods`, {
                            "apiVersion": "v1",
                            "kind": "Pod",
                            "metadata": {
                                "name": `${hashName}`,
                                "labels": {
                                    "environment": "labs",
                                    "app": `${hashName}`,
                                    "userId": me.userId.replace('@', '_')
                                }
                            },
                            "spec": {
                                "initContainers": [
                                    {
                                        "name": "mkdir",
                                        "image": "busybox",
                                        "command": ["mkdir", "-p", `/home/project/${filePath}/${userEmail}`],
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": "labs"
                                            }
                                        ]
                                    }
                                ],
                                "containers": [
                                    {
                                        "name": `${hashName}`,
                                        "image": "sanghoon01/theia-labs",
                                        "securityContext": {
                                            "privileged": true,
                                            "runAsUser": 0
                                        },
                                        "readinessProbe": {
                                            "httpGet": {
                                                "path": "/",
                                                "port": 3000,
                                                "initialDelaySeconds": 10,
                                                "periodSeconds": 5
                                            }
                                        },
                                        "env": [
                                            {
                                                "name": "COURSE",
                                                "value": `${course}`
                                            },
                                            {
                                                "name": "LABS",
                                                "value": `${lab}`
                                            },
                                            {
                                                "name": "CLAZZ",
                                                "value": `${clazz}`
                                            },
                                            {
                                                "name": "EMAIL",
                                                "value": `${userEmail}`
                                            },

                                        ],
                                        "resources": {
                                            "requests": {
                                                "memory": "2Gi"
                                            }
                                        },
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/var/run/docker.sock",
                                                "name": "dockersock"
                                            },
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": `labs/${filePath}/${userEmail}`
                                            },
                                            {
                                                "mountPath": "/root/.kube",
                                                "name": "test-storage",
                                                "subPath": `labs/${configPath}`
                                            }
                                        ],
                                    }
                                ],
                                "volumes": [
                                    {
                                        "name": "dockersock",
                                        "hostPath": {
                                            "path": "/var/run/docker.sock",
                                            "type": "File"
                                        }
                                    },
                                    {
                                        "name": "test-storage",
                                        "persistentVolumeClaim": {
                                            "claimName": "nfs"
                                        }
                                    }
                                ]
                            }
                        }).then(function () {
                            me.$http.post(`http://34.84.88.143/api/v1/namespaces/default/services`, {
                                "apiVersion": "v1",
                                "kind": "Service",
                                "metadata": {
                                    "labels": {
                                        "environment": "labs",
                                        "app": `${hashName}`,
                                        "userId": me.userId.replace('@', '_')
                                    },
                                    "name": `${hashName}`,
                                    "namespace": "default"
                                },
                                "spec": {
                                    "ports": [
                                        {
                                            "port": 3000,
                                            "protocol": "TCP",
                                            "targetPort": 8888
                                        }
                                    ],
                                    "selector": {
                                        "app": `${hashName}`
                                    },
                                    "sessionAffinity": "None",
                                    "type": "ClusterIP"
                                },
                                "status": {
                                    "loadBalancer": {}
                                }
                            }).then(
                                function () {
                                    var metaData = {
                                        email: userEmail,
                                        status: "start",
                                        startTime: new Date(),
                                        result: false
                                    }
                                    me.$minioClient.putObject('labs', `${filePath}/User_Lab_Metadata.json`, JSON.stringify(metaData))
                                    resolve(hashName)
                                }
                            )
                        })
                    }
                })
            }
        },

    }
</script>
<style>
    iframe {
        height: 100%;
        width: 100%;
    }
</style>

