<template>

</template>

<script>

    export default {
        name: "LabBase",
        components: {},
        props: {},
        computed: {
            myId() {
                return localStorage.getItem("email");
            },
            myHashCode() {
                return this.hashCode(this.courseId + '/runningClass/' + this.classId + '/labs/' + this.labId + '/' + this.myId)
            },
            isAdmin() {
                return (window.localStorage.getItem('authorized') == 'admin')
            },
            courseId() {

                return this.$route.params.courseId;
            },
            labId() {

                return this.$route.params.labId;
            },
            classId() {

                return this.$route.params.classId;
            },

        },
        data: {},
        beforeDestroy() {
        },

        created() {
            var me = this


        },
        methods: {

            /// underbar methods are ABSTRACT METHODS that must be implemented by derived classes

            _list(path, metadata) {
                throw new Error("must be implemented")
            },
            async _get(path) {
                throw new Error("must be implemented")

            },
            _put(path, string) {
                throw new Error("must be implemented")
            },
            _delete(path) {
                throw new Error("must be implemented")
            },

            async list(path) {
                return await this._list(path);
            },

            async listMetadata(path) {
                return await this._list(path, true);
            },
            async getStringStandalone(path) {
                return await this._getStandalone(path);
            },
            async getString(path) {
                return await this._get(path);
            },
            async delete(path) {
                return await this._delete(path)
            },
            async getObject(path) {
                var string = await this.getString(path);
                var data = JSON.parse(string);

                return data;
            },
            async getObjectStandalone(path) {
                var string = await this.getStringStandalone(path);
                var data = JSON.parse(string);

                return data;
            },
            async putObjectStandalone(path, obj) {
                var string = JSON.stringify(obj);
                return await this.putStringStandalone(path, string);
            },
            async putStringStandalone(path, string) {
                return await this._putStandalone(path, string);
            },
            async putString(path, string) {
                return await this._put(path, string);
            },
            async putObject(path, obj) {
                var string = JSON.stringify(obj);
                return await this.putString(path, string);
            },
            getLabInfo(labId) {
                var me = this
                if (!labId) labId = me.labId;

                return new Promise(async function (resolve) {
                    var labPath = `${me.courseId}/planed/${labId}/Lab_Metadata.json`

                    var labInfo = await me.getObject(labPath);
                    labInfo.labId = labId;

                    var markdown = await me.getString(`${me.courseId}/planed/${me.labId}/instruction.md`)
                    if (markdown)
                        try {
                            labInfo.instructionHtml = marked(markdown, {sanitize: true})
                        } catch (e) {
                            labInfo.instructionHtml = e.message
                        }

                    var theoryMD = await me.getString('minio', `${me.courseId}/planed/${me.labId}/theory.md`)
                    if (theoryMD) {
                        try {
                            labInfo.theoryHtml = theoryMD.includes('<') ? theoryMD : marked(theoryMD, {sanitize: true})
                        } catch (e) {
                            labInfo.theoryHtml = e.message
                        }
                    }
                    //todo: load javascript here
                    resolve(labInfo);
                })
            },
            async getLabStatus(labId) {
                var me = this;

                var statusFilesMd = await this.listMetadata(`${me.courseId}/runningClass/${me.classId}/labs/${labId}/status/`);

                var labStatus = {
                    status: 'READY'
                }

                statusFilesMd.forEach(item => {   //Event Sourcing:  replaying the histories takes you to the current state
                    if (item.name.endsWith('completed')) {
                        labStatus.status = 'completed';
                        labStatus.completedAt = item.lastModified;
                    } else if (item.name.endsWith('started')) {
                        labStatus.status = 'started';
                        labStatus.startedAt = item.lastModified;
                    }
                });

                return labStatus;
            },
            async getClassInfo() {
                var me = this
                var path = `${this.courseId}/runningClass/${this.classId}/Class_Metadata.json`
                console.log("************  ClassInfo  *************")
                var classInfo = await this.getObject(path);
                console.log(classInfo)
                if (classInfo.openClass) {
                    var enrolledPath = `${classInfo.courseId}/runningClass/${classInfo.classId}/enrolledUsers/`;
                    var userList = await me.list(enrolledPath);
                    userList.forEach(async function (users, idx) {
                        var email = users.replace(enrolledPath, '').replace(".txt", '')
                        if (idx % 5 == 0) {
                            var groupInfo = {"groupName": `${(idx / 5) + 1}조`, "users": []}
                            classInfo.groupedUsers.push(groupInfo)
                        }
                        var userInfo = await me.getObject(enrolledPath + email + '.txt');
                        var userImage = await me.getUserImage(email);
                        var tmpUserInfo = {
                            name: userInfo.userName,
                            email: email,
                            userImage: userImage
                        }
                        classInfo.groupedUsers[parseInt(idx / 5)].users.push(tmpUserInfo)
                    })
                    return classInfo
                } else {
                    // classInfo.groupedUsers.forEach(function () {
                    //
                    // })
                    return classInfo
                }
                // return await this.getObject(path);
            },
            async getCourseInfo() {
                return await this.getObject(`${this.courseId}/Course_Metadata.json`);
            },
            async getUserImage(email) {
                var me = this
                var userInfo = await me.getObject(`${me.courseId}/runningClass/${me.classId}/enrolledUsers/${email}.txt`)
                return userInfo.photoURL;
            },
            hashCode(s) {
                return s.split("").reduce(function (a, b) {
                    a = ((a << 5) - a) + b.charCodeAt(0);
                    return a & a
                }, 0);
            },


        }
    };
</script>
