<template>
    <div>
        <geometry-element
                selectable
                :movable="editMode"
                :resizable="editMode"
                connectable
                :deletable=editMode
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:rotateShape="onRotateShape"
                v-on:labelChanged="onLabelChanged"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
                v-on:contextmenu.prevent.stop="handleClick($event)"
        >

            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
                        'fill-r': 1,
                        'fill-cx': .1,
                        'fill-cy': .1,
                        'stroke-width': 1.4,
                        'stroke': '#fbc02d',
                        fill: '#fbc02d',
                        'fill-opacity': 1,
                        r: '1',
                        'z-index': '998'
                    }"
            ></geometry-rect>

            <sub-controller
                    v-if="value.status"
                    :image="'subprocess.png'"
                    @click.prevent.stop="handleClick($event)"
            ></sub-controller>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="30"
                        :sub-top="0"
                        :sub-left="0"
                        :text="'Pod'">
                </text-element>
                <image-element
                        :image="imgSrc"
                        :sub-top="5"
                        :sub-left="5"
                        :sub-width="30"
                        :sub-height="30"
                >

                </image-element>
            </sub-elements>

            <sub-elements>
                <circle-element
                        v-if="value.status"
                        :sub-bottom="-15"
                        :sub-width="30"
                        :sub-height="30"
                        :sub-align="'center'"
                        :sub-style="{
                            'stroke': statusColor,
                            fill: statusColor,
                            'fill-opacity': 1,
                        }">
                </circle-element>
            </sub-elements>
        </geometry-element>

        <property-panel
                v-if="openPanel"
                v-model="value"
                :img="imgSrc">
        </property-panel>

        <vue-context-menu
            :elementId="value._type"
            :options="menuList"
            :ref="'vueSimpleContextMenu'"
            @option-clicked="optionClicked">
        </vue-context-menu>
    </div>
</template>

<script>
    import Element from '../Kube-Element'
    import PropertyPanel from './PodPropertyPanel'

    export default {
        mixins: [Element],
        name: 'pod',
        components: {
            "property-panel": PropertyPanel
        },
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'Pod'
            },
            imgSrc() {
                return `${window.location.protocol + "//" + window.location.host}/static/image/symbol/kubernetes/pod.svg`
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namespace: '',
                    namespaceId: '',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({}),
                        'angle': 0,
                    },
                    object: {
                        "apiVersion": "v1",
                        "kind": "Pod",
                        "metadata": {
                            "name": "",
                            "labels": {
                                "name": ""
                            }
                        },
                        "spec": {
                            "containers": [
                                {
                                    "name": "",
                                    "image": "",
                                    "ports": [
                                        {
                                            "containerPort": 80
                                        }
                                    ]
                                }
                            ]
                        }
                    },
                    outboundVolumes: [],
                    connectableType: [ "PersistentVolumeClaim" ],
                    status: null,
                }
            },
            name() {
                try {
                    return this.value.object.metadata.name;
                } catch (e) {
                    return "Untitled";
                }
            },
            outboundVolumeNames() {
                try {
                    var names;

                    this.value.outboundVolumes.forEach(element => {
                        names += element.object.metadata.name + ","
                    });

                    return names;

                } catch (e) {
                    return "";
                }
            },
            namespace: {
                get: function () {
                    return this.value.object.metadata.namespace
                },
                set: function (newVal) {
                    this.value.object.metadata.namespace = newVal
                }
            }
        },
        data: function () {
            return {
                menuList : [
                    { name: "View Terminal" },
                    { name: "Delete" }
                ]
            };
        },
        created: function () {

        },
        mounted: function () {

            var me = this;

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if (obj.state == "addRelation" && obj.element && obj.element.targetElement
                    && obj.element.targetElement._type == "PersistentVolumeClaim") {
                    console.log("inner")
                    me.value.outboundVolumes.push(obj.element.targetElement);
                }

                if (obj.state == "deleteRelation" && obj.element && obj.element.targetElement
                    && obj.element.targetElement._type == "PersistentVolumeClaim") {

                    me.value.outboundVolumes.splice(me.value.outboundVolumes.indexOf(obj.element.targetElement), 1);
                }
                if (obj.state == "changeName") {
                    me.namespace = obj.element
                }

                if(obj.state == "get" && obj.element && obj.element.kind == me.value.object.kind) {
                    me.value.status = obj.element.status
                    me.setStatus()
                    me.refresh()
                }

            })

        },
        beforeDestroy() {
            var me = this
            var obj = {
                state: "deleteElement",
                element: me.value.elementView.id
            }
            this.$EventBus.$emit(`${me.value.namespaceId}`, obj)
        },
        watch: {
            name(appName) {
                this.value.object.metadata.labels.name = appName;
                this.value.object.spec.containers[0].name = appName;
            },
            outboundVolumeNames(names) {
                this.value.object.spec.volumes = [];
                var me = this;
                var i = 0;
                this.value.outboundVolumes.forEach(element => {
                        me.value.object.spec.volumes.push(
                            {
                                "name": "volume" + (++i),
                                "persistentVolumeClaim": {
                                    "claimName": element.object.metadata.name
                                }
                            }
                        );
                    }
                );

            }
        },

        methods: {
            setStatus() {
                var me = this
                
                if(me.value.status.containerStatuses) {
                    // var state = me.value.status.containerStatuses[0].state
                    // var stateKey = Object.keys(state)
                    // console.log(me.value.status.containerStatuses[0].ready)
                    if(me.value.status.containerStatuses[0].ready) {
                        me.changeStatusColor('success')
                    } else {
                        me.changeStatusColor('waiting')
                    }
                }    

            },
        },
    }
</script>

