<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!checkElementReadOnly && movingElement"
                :resizable="!checkElementReadOnly && movingElement"
                :deletable=!checkElementReadOnly
                connectable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"

                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="value.name"
                :image.sync="setImg"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5099F7',
          fill: '#5099F7',
          'fill-opacity': 1,
          r: '1', 'z-index': '998'
        }"
            >
            </geometry-rect>
            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5099F7',
          fill: '#5099F7',
          'fill-opacity': .5,
          r: '1', 'z-index': '998'
        }"
            >
            </geometry-rect>
            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Command >>'">
                </text-element>
                <image-element
                        v-if="alertImage"
                        v-bind:image="failed_image"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>

        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'Command'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/command.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->


        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'Command'"
                :errorMsg="errorMsg"
                :isRead="checkElementReadOnly"
                @closePanel="closePanel"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/command.png'"

        >
        </modeling-property-panel>


    </div>
</template>

<script>
    import Element from './EventStormingElement'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');

    var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'command-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.value.name == '' && !me.attachedAggregate && !me.attachedBoundedContext) {
                    return 'Element must be in a Bounded Context, Must be attached to an Aggregate and Must has a name.'
                } else if (!me.attachedAggregate) {
                    return 'Must be attached to an Aggregate.'
                } else if (!me.attachedBoundedContext) {
                    return 'Element must be in a Bounded Context.'
                } else if (me.value.name == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this

                if (!me.attachedBoundedContext || !me.attachedAggregate || me.value.name == '') {
                    return true
                } else {
                    return false
                }
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Command'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',

                    aggregate: {},
                    boundedContext: {},

                    // alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({}),
                        'z-index': 999
                    },
                    //new pannel
                    isRestRepository: true,
                    controllerInfo: {
                        apiPath: '',
                        method: 'GET'
                    },
                    restRepositoryInfo: {
                        method: 'GET'
                    },
                    relationEventInfo: [],
                }
            }
        },
        data: function () {
            return {
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {

        },
        watch: {

        },
        methods: {
            validate() {
                var me = this
                var designer = me.getComponent('modeling-designer')

                me.attachedBoundedContext = designer.attachedLists.boundedContextLists.some(bc => {
                    return me.isAttached(bc);
                })
                me.attachedAggregate = designer.attachedLists.aggregateLists.some(function (agg) {
                    return me.isAttached(agg)
                })

            }


        }
    }
</script>


