<template>
    <v-container fluid>
        <v-text-field
                label="Search Project"
                outlined
                v-model="tempSearch"
                style="max-width: 1300px; margin-right: 10px;margin-left: 12px;"
        ></v-text-field>
        <v-tabs
                v-model="selectedTab"
                background-color="transparent"
                color="blue darken-1"
                left
        >
            <v-tabs-slider color="green lighten-2"></v-tabs-slider>

            <v-tab
                    v-for="(tab,index,keys) in labsTabLists"
                    :key="keys"
                    :value="keys"
            >
                {{tab.tabName}}
            </v-tab>
            <v-tab
                    :to="'/'"
            >
                Storming
            </v-tab>
            <v-tab-item
                    align="center"
                    v-if="teacherClassList == undefined || clazzList == undefined"
                    v-for="(tab,index,keys) in labsTabLists"
                    :key="keys"
                    :value="keys"
            >
                <v-progress-circular :size="70" color="primary" indeterminate></v-progress-circular>
            </v-tab-item>
            <v-tab-item
                    v-else
                    v-for="(tab,index,keys) in labsTabLists"
                    :key="keys"
                    :value="keys"
            >
                <v-card v-if="selectedTab == 1">
                    <v-row>
                        <v-col v-if="filteredTeacherClazzList.length < 1">
                            데이터가 없습니다.
                        </v-col>
                        <v-col v-else v-for="(clazz, index) in filteredTeacherClazzList" :key="index" md="3">
                            <v-card outlined
                                    class="mx-auto"
                                    style="width: 400px; height: 480px; justify-content: center">
                                <!--                                <v-btn @click="thumbnailImg(clazz)"></v-btn>-->
                                <v-img :aspect-ratio="16/9"
                                       max-width="400px"
                                       max-height="200px"
                                       :src="clazz.thumbnail"></v-img>
                                <v-card-title style="height:120px">
                                    <!--                        {{course}}-->
                                    {{clazz.className}}
                                </v-card-title>
                                <v-card-text style="height:108px">
                                    코스명 : {{clazz.course ? clazz.course.courseDesc : clazz.courseId}} <br>
                                    클래스 시작 : {{clazz.classStartDate}} <br>
                                    클래스 종료 : {{clazz.classEndDate}} <br>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text
                                           :to="'/courses/'+clazz.courseId+'/'+clazz.classId">
                                        입장
                                    </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-card>
                <v-card v-else-if="selectedTab == 0">
                    <v-row>
                        <v-col v-if="filteredClazzList.length < 1">
                            데이터가 없습니다.
                        </v-col>
                        <v-col v-else v-for="(clazz, index) in filteredClazzList" :key="index" md="3">
                            <v-card outlined
                                    class="mx-auto"
                                    style="width: 400px; height: 480px; justify-content: center">
                                <!--                                <v-btn @click="thumbnailImg(clazz)"></v-btn>-->
                                <v-img :aspect-ratio="16/9"
                                       max-width="400px"
                                       max-height="200px"
                                       :src="clazz.thumbnail"
                                ></v-img>
                                <!--                                <v-img-->
                                <!--                                        :src="projectInfomation['img']"-->
                                <!--                                        max-width="400"-->
                                <!--                                        max-height="300"-->
                                <!--                                >-->
                                <v-card-title style="height:120px">
                                    <!--                        {{course}}-->
                                    {{clazz.className}}
                                </v-card-title>
                                <v-card-text style="height:108px">
                                    코스명 : {{clazz.course ? clazz.course.courseDesc : clazz.courseId}} <br>
                                    클래스 시작 : {{clazz.classStartDate}} <br>
                                    클래스 종료 : {{clazz.classEndDate}} <br>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text
                                           @click="joinClass(clazz)">
                                        입장
                                    </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-card>
            </v-tab-item>
        </v-tabs>
    </v-container>
</template>

<script>
    var Minio = require('minio');

    export default {
        name: "ClazzListPage",
        components: {},
        data: () => ({
            clazzList: undefined,
            teacherClassList: undefined,
            overlay: false,
            selectedTab: 0,
            email: '',
            search: '',
            loaded: false,
            labsTabLists: [{tabName: '수강중인 클래스', key: 'study'}, {tabName: '강의중인 클래스', key: 'lecture'}],
            tempSearch: ''
        }),
        computed: {
            testSearch() {
                return this.search
            },
            filteredClazzList() {
                var me = this
                var filter = []
                filter = me.clazzList

                if (me.testSearch) {
                    return filter.filter(clazz => {
                        if (clazz.className != undefined) {
                            return clazz.className.toLowerCase().includes(me.search.toLowerCase())
                        }

                    })
                } else {
                    return filter
                }
            },
            filteredTeacherClazzList() {
                var me = this
                var filter = []
                filter = me.teacherClassList

                if (me.testSearch) {
                    return filter.filter(clazz => {
                        if (clazz.className != undefined) {
                            return clazz.className.toLowerCase().includes(me.search.toLowerCase())
                        }

                    })
                } else {
                    return filter
                }
            },
            sortedClazzList() {
                var me = this
                var o = me.clazzList;
                var sorted = {},
                    key, a = [];
                // 키이름을 추출하여 배열에 집어넣음
                for (key in o) {
                    if (o.hasOwnProperty(key)) a.push(key);
                }
                // 키이름 배열을 정렬
                a.sort();
                // 정렬된 키이름 배열을 이용하여 object 재구성
                for (key = 0; key < a.length; key++) {
                    sorted[a[key]] = o[a[key]];
                }
                return sorted;
            },
        },
        watch: {
            "tempSearch":
                _.debounce(
                    function (newVal) {
                        var me = this
                        me.search = newVal
                    }, 400
                ),
        },
        mounted() {
            var me = this
            this.email = localStorage.getItem("email");
            this.getClazzList();


        },
        methods: {
            toDataURL(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.onload = function () {
                    var reader = new FileReader();
                    reader.onloadend = function () {
                        callback(reader.result);
                    }
                    reader.readAsDataURL(xhr.response);
                };
                xhr.open('GET', url);
                xhr.responseType = 'blob';
                xhr.send();
            },
            async thumbnailImg(clazz) {
                var me = this
                var checkImg = await this.checkExistImg(clazz)
                if (!checkImg) {
                    // 체크 이미지가 없을땐 기본 이미지를 뿌려줌
                    var path = clazz.courseId + "/default.png";

                    var img = await this.getThumbnailImg(path)
                    return img
                }
                var img = await this.getThumbnailImg(checkImg)
                return img
                // 체크 이미지가 있을때 타는 곳
            },
            getThumbnailImg(path) {
                return new Promise((resolve, reject) => {
                    this.$minioClient.getObject('labs', path, function (err, imgDataStream) {
                        if (err) {
                            reject(err);
                            return;
                        }
                        if (!imgDataStream) {
                            reject(new Error("no stream connected"));
                            return;
                        }
                        imgDataStream.on('data', function (imgChunk) {
                            var base64Data = btoa(String.fromCharCode.apply(null, imgChunk))
                            if (path.includes('png')) {
                                resolve('data:image/png;base64,' + base64Data)
                            } else if (path.includes('jpg')) {
                                resolve('data:image/jpg;base64,' + base64Data)
                            }
                        })

                        imgDataStream.on('error', function (err) {
                            reject(err);
                        })
                    })
                });

            },
            checkExistImg(clazz) {
                var listObj = [];
                var checkImg = false
                var imgPath = ''
                var path = clazz.courseId + "/runningClass/" + clazz.classId + '/';
                return new Promise((resolve, reject) => {
                    this.$minioClient.listObjects('labs', path, false)
                        .on('data', function (obj) {
                            if (obj.name) listObj.push(obj.name)
                        })
                        .on('error', function (err) {
                            reject(err);
                        })
                        .on('end', function (err) {
                            listObj.forEach(function (item) {
                                if (item.includes('.jpg') || item.includes('.png')) {
                                    imgPath = item
                                    checkImg = true
                                }
                            });
                            if (checkImg) {
                                resolve(imgPath);
                            } else
                                resolve(false)
                        })
                });
            },
            joinClass(clazz) {
                var me = this
                var userId = localStorage.getItem('email')
                var userName = localStorage.getItem('userName');
                var uploadObject = {
                    userName: userName,
                    photoURL: localStorage.getItem('picture')
                }
                me.$minioClient.putObject('labs', `${clazz.courseId}/runningClass/${clazz.classId}/enrolledUsers/${userId}.txt`, JSON.stringify(uploadObject), function (err, etag) {
                })
                this.$router.push('/courses/' + clazz.courseId + '/' + clazz.classId)
            },
            async getClazzList() {
                var me = this

                this.$minioClient.listObjects('labs', '', false)
                    .on('data', function (rootObj) {
                        if (rootObj.prefix) {
                            console.log(rootObj)
                            me.$minioClient.listObjects('labs', rootObj.prefix + 'runningClass/', false)
                                .on('data', function (courseObj) {
                                    me.$minioClient.listObjects('labs', courseObj.prefix, false)
                                        .on('data', function (classObj) {
                                            if (classObj.name && classObj.name.includes('Class_Metadata') && classObj.size != 0) {
                                                console.log(classObj.name)
                                                me.$minioClient.getObject('labs', classObj.name, function (err, dataStream) {
                                                    if (err) {
                                                        return console.log(err)
                                                    }
                                                    dataStream.on('data', function (chunk) {
                                                        var string = new TextDecoder("utf-8").decode(chunk);
                                                        var classMetadata = JSON.parse(string)
                                                        if (me.clazzList == undefined) {
                                                            me.clazzList = []
                                                        }
                                                        if (me.teacherClassList == undefined) {
                                                            me.teacherClassList = []
                                                        }
                                                        if (classMetadata.teacherId == me.email && me.email != null) {
                                                            // 강사
                                                            me.teacherClassList.push(classMetadata)
                                                        }
                                                        // filtering
                                                        if (classMetadata.openClass) {
                                                            var nameParts = classObj.name.split("/");
                                                            classMetadata.classId = nameParts[2];
                                                            classMetadata.courseId = nameParts[0];

                                                            me.$minioClient.getObject('labs', classMetadata.courseId + "/Course_Metadata.json", function (err, dataStream) {
                                                                if (err) {
                                                                    return console.log(err)
                                                                }
                                                                dataStream.on('data', async function (chunk) {
                                                                    var string = new TextDecoder("utf-8").decode(chunk);
                                                                    var courseMetadata = JSON.parse(string)
                                                                    classMetadata.course = courseMetadata;
                                                                    classMetadata.thumbnail = await me.thumbnailImg(classMetadata)
                                                                    //notify manually
                                                                    classMetadata.__ob__.dep.notify()
                                                                })

                                                                dataStream.on('error', function (err) {
                                                                    console.log(err)
                                                                })
                                                            })

                                                            me.clazzList.push(classMetadata)
                                                        } else {
                                                            classMetadata.groupedUsers.some(item => {
                                                                return item.users.some(user => {
                                                                        if (user.email == me.email) {

                                                                            // get course info
                                                                            var nameParts = classObj.name.split("/");
                                                                            classMetadata.classId = nameParts[2];
                                                                            classMetadata.courseId = nameParts[0];

                                                                            me.$minioClient.getObject('labs', classMetadata.courseId + "/Course_Metadata.json", function (err, dataStream) {
                                                                                if (err) {
                                                                                    return console.log(err)
                                                                                }
                                                                                dataStream.on('data', async function (chunk) {
                                                                                    var string = new TextDecoder("utf-8").decode(chunk);
                                                                                    var courseMetadata = JSON.parse(string)
                                                                                    classMetadata.course = courseMetadata;
                                                                                    classMetadata.thumbnail = await me.thumbnailImg(classMetadata)
                                                                                    //notify manually
                                                                                    classMetadata.__ob__.dep.notify()
                                                                                })

                                                                                dataStream.on('error', function (err) {
                                                                                    console.log(err)
                                                                                })
                                                                            })

                                                                            me.clazzList.push(classMetadata)
                                                                            return true;

                                                                        }
                                                                    }
                                                                )
                                                            })
                                                        }
                                                    });
                                                    dataStream.on('error', function (err) {
                                                        console.log(err)
                                                    })
                                                })
                                            }
                                        })
                                        .on('end', function () {
                                        })
                                })
                                .on('error', function (err) {
                                    alert(err);
                                })

                        }
                    })
                    .on('error', function (err) {
                        alert(err);
                    })

            },
        }
    };
</script>
