<template>
    <v-container fluid>
        <v-row>
            <v-col align="center">
                <h1>
                {{classInfo.className}}</h1>

                <v-timeline :dense="$vuetify.breakpoint.smAndDown">
                    <v-timeline-item
                        v-for="(lab, index) in labs" :key="index"
                        color="purple lighten-2"
                        fill-dot
                        :left = "index % 2 == 0"
                        :right = "index % 2 == 1"
                    >

                        <v-card style="width: 400px" v-if="lab && lab.labId">
                            <!--                    <img v-if="a.imgSrc"-->
                            <!--                         :src="labs.imgSrc"-->
                            <!--                         style="width: 300px; height: 150px"-->
                            <!--                    >-->
                            <v-card-title>
                                <v-chip color="primary">{{lab.labId}}</v-chip>
                                <v-chip v-if="lab.labStatus && lab.labStatus.status=='completed'" color="green" text-color="white" class="ma-2">완료</v-chip>
                                <v-chip v-if="lab.labStatus && lab.labStatus.status=='started'" color="red" text-color="white"  class="ma-2">진행중</v-chip>
                                {{lab.labName}}
                            </v-card-title>
                            <v-card-text>
                                {{lab.labScenario}} <br>
                                {{lab.labTime}}분 이내 완료
                            </v-card-text>
                            <v-card-actions>

                                <v-spacer></v-spacer>
                                <v-btn :to="$route.fullPath + ($route.fullPath.endsWith('/') ? '':'/') +  lab.labId + '/class-room'">입장</v-btn>
<!--                                <v-btn  v-else :to="$route.fullPath + ($route.fullPath.endsWith('/') ? '':'/') +  lab.labId + '/' + myId">입장</v-btn>-->
                            </v-card-actions>
                        </v-card>
                    </v-timeline-item>
                </v-timeline>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import LabBase from './LabBase'

    export default {
        name: "ClassDetail",

        components: {},
        mixins: [LabBase],

        data: () => ({
            courseInfo: null,
            classInfo: null,
            labs:[]
        }),

        computed: {
        },
        watch: {},
        mounted() {
            this.load();
            window.document.title='라운지'
        },
        methods: {
            async load() {
//                this.courseInfo = await this.getCourseInfo();

                try{
                    this.classInfo = await this.getClassInfo();

                    var me = this;
                    var labsByLabId = {}
                    var labIndexByLabId = {}

                    for(var i=0; i<this.classInfo.labsList.length; i++){
                        labIndexByLabId[this.classInfo.labsList[i]] = i;

                        me.getLabInfo(this.classInfo.labsList[i]).then((labInfo)=>{
                            if(labInfo){
                                me.labs[labIndexByLabId[labInfo.labId]] = labInfo;
                                me.labs.__ob__.dep.notify();
                                
                                me.getLabStatus(labInfo.labId).then(labStatus=>{
                                    labInfo.labStatus = labStatus
                                });
                            }
                        });
                    }

                }catch(e){
                    alert(e)
                    console.log(e)
                }
            },
        }
    };
</script>
