<template>
    <div>
        <group-element
                :selectable="movingElement"
                :movable="!checkElementReadOnly && movingElement"
                :resizable="!checkElementReadOnly && movingElement"
                :deletable=!checkElementReadOnly
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"

                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addToGroup="onAddToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="value.name"
                :image.sync="setImg"
                :_style="{
                'vertical-align': 'top',
                'font-weight': 'bold',
                'font-size': '16'
                }"
        >

            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#000000',
          'fill-opacity': 1,
          'vertical-align': 'top',
          'font-weight': 'bold',
          'font-size': '16',
          'r': '1'
        }">
            </geometry-rect>

            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#000000',
          'fill': '#e3e1e1',
          'fill-opacity': .5,
          'vertical-align': 'top',
          'font-weight': 'bold',
          'font-size': '16',
          'r': '1'
        }">
            </geometry-rect>

            <storming-sub-controller :type="type"></storming-sub-controller>

            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

        </group-element>

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :errorMsg="errorMsg"
                :isRead="checkElementReadOnly"
                titleName="BoundedContext"
                @closePanel="closePanel"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from './EventStormingElement'
    import GroupElement from "../../../opengraph/shape/GroupElement";
    import ImageElement from "../../../opengraph/shape/ImageElement";

    var changeCase = require('change-case');
    var pluralize = require('pluralize');


    export default {
        components: {ImageElement, GroupElement},
        mixins: [Element],
        name: 'bounded-context-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.value.name == '') {
                    return 'Please give a name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this
                if (me.value.name == '') {
                    return true
                } else {
                    return false
                }
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.BoundedContext'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    aggregates: [],
                    policies: [],
                    viewes: [],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 300,
                        'height': 300,
                        'style': JSON.stringify({})
                    },
                    portGenerated: 0,
                    groups: [],
                    tempId: ''
                }
            }
        },
        data: function () {
            return {
                isRead: false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                // isValueChanged: false,
            };
        },
        created: function () {
        },
        watch: {},
        mounted: function () {
            var me = this

            var boundedId = this.value.elementView.id

            var $tr = $('#' + boundedId);
            $tr.parent().children().first().before($tr)

        },
        methods: {}
    }
</script>


