<template></template>

<script>
    var Minio = require('minio');
    import LabBaseAbstract from './LabBaseAbstract'

    export default {
        name: "LabBaseMinio",
        mixins: [LabBaseAbstract],

        computed: {
            minioClient() {
                return new Minio.Client({
                    endPoint: "35.200.116.236",
                    port: 9000,
                    useSSL: false,
                    accessKey: "minio",
                    secretKey: "minio123"
                })
            },
            standaloneClient() {
                return new Minio.Client({
                    endPoint: "34.84.88.143",
                    port: 9000,
                    useSSL: false,
                    accessKey: "minio",
                    secretKey: "minio123"
                })
            }

        },
        methods: {
            _list(path, metadata) {
                return this.minio_list(path, metadata);
            },
            async _getStandalone(path) {
                return this.minio_getStandalone(path)
            },
            _putStandalone(path, string) {
                return this.minio_putStandalone(path, string)
            },
            async _get(path) {
                return this.minio_get(path)
            },
            _put(path, string) {
                return this.minio_put(path, string)
            },
            _delete(path) {
                return this.minio_delete(path)
            },
            minio_list(path, metadata) {
                var me = this;
                var listObj = [];
                return new Promise((resolve, reject) => {
                    this.$minioClient.listObjects('labs', path, false)
                        .on('data', function (obj) {
                            listObj.push(obj)
                        })
                        .on('error', function (err) {
                            reject(err);
                        })
                        .on('end', function (err) {

                            var list = [];

                            listObj.sort(function (a, b) {
                                return a.lastModified - b.lastModified;
                            })

                            listObj.forEach(obj => {
                                list.push(obj.name)
                            })

                            if (metadata)
                                resolve(listObj)
                            else
                                resolve(list);
                        })
                });
            },
            async minio_presignedURL(path) {
                var me = this
                return new Promise((resolve, reject) => {
                    me.minioClient.presignedUrl('GET', 'labs', path, 24 * 60 * 60, function (err, presignedUrl) {
                        if (err) return reject(err)
                        resolve(presignedUrl)
                    })
                });
            },
            async minio_getStandalone(path) {
                var me = this

                var exist = await me._list(path);
                if (exist.length == 0) return null;
                return new Promise((resolve, reject) => {
                    me.standaloneClient.getObject('labs', path, function (err, labDataStream) {
                        if (err) {
                            resolve(null)
                            //reject(err);
                            return;
                        }
                        if (!labDataStream) {
                            resolve(null)
                            //reject(new Error("no stream connected"));
                            return;
                        }
                        labDataStream.on('data', function (labChunk) {
                            var string = new TextDecoder("utf-8").decode(labChunk)
                            resolve(string);
                        })

                        labDataStream.on('error', function (err) {
                            resolve(null)
                            //reject(err);

                        })
                    })
                });


            },
            minio_putStandalone(path, string) {
                var me = this;
                return new Promise((resolve, reject) => {
                    me.standaloneClient.putObject('labs', path, string, function (err, etag) {
                        if (err == null && etag) {
                            resolve(etag);
                        } else
                            reject(err);

                    });
                });
            },
            async minio_get(path) {
                var me = this

                var exist = await me._list(path);
                if (exist.length == 0) return null;
                return new Promise((resolve, reject) => {
                    me.minioClient.getObject('labs', path, function (err, labDataStream) {
                        if (err) {
                            resolve(null)
                            //reject(err);
                            return;
                        }
                        if (!labDataStream) {
                            resolve(null)
                            //reject(new Error("no stream connected"));
                            return;
                        }
                        labDataStream.on('data', function (labChunk) {
                            var string = new TextDecoder("utf-8").decode(labChunk)
                            resolve(string);
                        })

                        labDataStream.on('error', function (err) {
                            resolve(null)
                            //reject(err);

                        })
                    })
                });


            },
            minio_put(path, string) {
                var me = this;
                return new Promise((resolve, reject) => {
                    me.minioClient.putObject('labs', path, string, function (err, etag) {
                        if (err == null && etag) {
                            resolve(etag);
                        } else
                            reject(err);

                    });
                });
            },
            minio_delete(path) {
                var me = this
                return new Promise((resolve, reject) => {
                    me.minioClient.removeObject('labs', path, function (err) {
                        if (err) {
                            reject(err)
                        }
                        resolve()
                    })
                })

            },


        }
    };
</script>
