<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!checkElementReadOnly && movingElement"
                :resizable="!checkElementReadOnly && movingElement"
                :deletable=!checkElementReadOnly
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label="value.name"
                :image.sync="setImg"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': .5,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Aggregate >>'">
                </text-element>
                <image-element
                        v-if="alertImage"
                        v-bind:image="failed_image"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'Aggregate'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/aggregate.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'Aggregate'"
                :errorMsg="errorMsg"
                :isRead="checkElementReadOnly"
                @closePanel="closePanel"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/aggregate.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    // import Element from '../../modeling/Element'
    import Element from './EventStormingElement'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    // var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'aggregate-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this

                if (me.value.name == '' && !me.attachedBoundedContext) {
                    return 'Element must be in a Bounded Context and Must has a name.'
                } else if (!me.attachedBoundedContext) {
                    return 'Element must be in a Bounded Context.'
                } else if (me.value.name == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }

            },
            alertImage() {
                var me = this

                if ( !me.attachedBoundedContext || me.value.name == '' ) {
                    return true
                } else {
                    return false
                }

            },
            namePascalCase() {
                var me = this
                return me.name.charAt(0).toUpperCase() + me.name.slice(1)
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Aggregate'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({})
                    },
                    boundedContext: {},
                    aggregateRoot: {
                        _type: "org.uengine.modeling.model.AggregateRoot",
                        fieldDescriptors:
                            [
                                {
                                    _type: "org.uengine.model.FieldDescriptor",
                                    name: "id",
                                    className: "Long",
                                    nameCamelCase: 'id',
                                    namePascalCase: 'Id',
                                    isKey: true
                                }
                            ],
                    },
                    events: [],
                    commands: [],
                }

            },

        },
        data: function () {
            return {
                itemH: 200,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
            };
        },
        created: function () {

        },
        watch: {

        },
        mounted: function () {
            var me = this

        },
        methods:{
            validate() {
                var me = this
                var designer = me.getComponent('modeling-designer')

                me.attachedBoundedContext = designer.attachedLists.boundedContextLists.some(bc => {
                    return me.isAttached(bc);
                })

            }
        }

    }
</script>

