</template>

<script>
    import firebase from 'firebase'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');


    export default {
        name: 'modeling-element-base',
        props: {
            value: Object,
        },
        created: function () {
            var me = this;

            me.parmUid = this.$route.params.userUid;
            me.parmType = this.$route.params.projectType
            me.parmProjectId = this.$route.params.projectId
            me.parmVersion = this.$route.params.projectVersion;


            me.messageRef = firebase.database().ref(`/${me.$route.params.author}/${me.$route.params.projectName}`);

        },
        data: function () {
            return {
                _id: null,
                rotateMove: false,
                tmpWidth: 0,
                tmpHeight: 0,
                messageRef: {},
                isException: 'NO',
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                isUndoRedo: false,
                checkValue: false,
                selected: false,
                openPanel: false,
                setImg: '',
                namePanel: '',
                //multiEdit
                movingElement: true,
                newEditUserImg: [],
                attachedBoundedContext: false,
                attachedAggregate: false,

                parmUid: null,
                parmType: null,
                parmProjectId: null,
                parmVersion: null,
                isRead: false,
                specVersion: '1.0'
                // isCustomMoveActionExist: false,

            }
        },
        computed: {

            type() {
                return ''
            },
            code() {
                return ''
            },
            style: {
                get: function () {
                    var style;
                    //스타일이 없다면 디폴트 스타일을 사용한다.
                    if (this.value) {
                        if (this.value.elementView)
                            style = this.value.elementView.style;
                        else
                            style = this.value.relationView.style;
                    }

                    if (style) {
                        var jsonStyle = JSON.parse(style);
                        if ($.isEmptyObject(jsonStyle)) {
                            return this.defaultStyle;
                        } else {
                            return jsonStyle;
                        }
                    } else {
                        return this.defaultStyle;
                    }
                },
                set: function (val) {
                    if (this.value) {
                        if (this.value.elementView)
                            this.value.elementView.style = JSON.stringify(val);
                        else
                            this.value.relationView.style = JSON.stringify(val);
                    }
                }
            },


        },
        watch: {
            "value.elementView.height": function () {
                this.refreshImg()
            },
            "movingElement": function () {
                this.refreshImg()
            },
            "alertImage": function () {
                this.refreshImg()
            },
            "newEditUserImg": function () {
                this.refreshImg()
            },
            "value.elementView": {
                deep: true,
                handler: function () {
                    var me = this
                    me.validate()

                }
            },

        },
        mounted: function () {
            var me = this

            var designer = me.getComponent('modeling-designer');

            $(document).keydown((evt) => {
                if (evt.keyCode == 46 || evt.keyCode == 8) {

                    if (me.selected && !me.openPanel) {

                        console.log("============== Storage Location Search Test 1-1 (Back Delete) ============= ")
                    }
                }
            });

            this.$nextTick(function () {
                me.$EventBus.$on('checkSpecVersion', function (newVal) {
                    me.specVersion = newVal
                })
            })


            if (me.value._type == 'org.uengine.modeling.model.Relation') {

                me.$EventBus.$on(`${me.value.relationView.id}`, function (obj) {
                    if (obj.state == 'delete') {
                        me.deleteActivity(obj)
                    }

                    if (obj.state == 'relationMove') {
                        // designer.isReceiveQueue = true
                        me.value.relationView.value = obj.element
                    }
                })

            } else {
                me.$EventBus.$on(me.value.elementView.id, function (obj) {
                    if (obj.state == 'delete') {
                        me.deleteActivity(obj)
                    }

                    if (obj.state == 'elementMove') {
                        // designer.isReceiveQueue = true

                        var afterMove = JSON.parse(obj.element)
                        me.value.elementView.x = afterMove.x
                        me.value.elementView.y = afterMove.y
                        me.value.elementView.width = afterMove.width
                        me.value.elementView.height = afterMove.height

                        me.$nextTick(function () {
                            me.movingElement = true
                        })

                    }

                    if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectedOn') {

                        if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                            me.newEditUserImg.push(obj)
                        }

                    } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectedOff') {
                        if (me.newEditUserImg.length > 0) {
                            me.newEditUserImg.forEach(function (user, idx) {
                                if (user.uid == obj.uid) {
                                    me.newEditUserImg[idx] = null
                                }
                            })
                            me.newEditUserImg = me.newEditUserImg.filter(n => n)

                        }
                    }


                })
            }

        }
        ,
        beforeDestroy() {
            if (this.value.elementView) {
                this.$EventBus.$off(`${this.value.elementView.id}`)
            } else {
                this.$EventBus.$off(`${this.value.relationView.id}`)
            }

        }
        ,
        methods: {
            showProperty() {
                this.openPanel = true
            },
            closePanel() {
                this.openPanel = false
            },
            refreshImg() {
                var me = this
                this.setImg = 'test'
                me.$nextTick(function () {
                    if (this.setImg == 'test') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'test'
                    }
                })
            },

            validate() { // validate 
            }
            ,
            delayedRelationMove(vertices) {
                var me = this
                var designer = me.getComponent('modeling-designer');


                var orgineVertices = JSON.parse(JSON.stringify(me.value.relationView.value))
                var newVertices = []
                var offsetVertices

                vertices.forEach(function (ver, index) {
                    newVertices.push([ver.x, ver.y])
                })

                offsetVertices = JSON.stringify(newVertices)
                console.log(orgineVertices, me.value.relationView.value)

                // if (designer.specVersion == '2.0' && (designer.parmType == 'mine' && designer.sharedCount > 0) || designer.parmType == 'share' && !designer.isRead) {
                if (me.specVersion == '2.0' && !me.isRead) {
                    var projectId = localStorage.getItem('projectId') == null ? designer.parmProjectId : localStorage.getItem('projectId')
                    firebase.database()
                        .ref('definitions/' + projectId + '/queue')
                        .push({
                            state: 'relationMove',
                            editUid: localStorage.getItem('uid'),
                            relationId: me.value.relationView.id,
                            before: orgineVertices,
                            after: offsetVertices,
                            timeStamp: new Date().getTime(),
                        })
                } else {
                    alert('No Match Condition.')
                    window.location.reload(true);
                }

            },

            delayedMove(dx, dy, dw, dh, du, dlw, dl, dr) {
                var me = this
                var designer = me.getComponent('modeling-designer');

                var offsetX, offsetY, offsetW, offsetH

                var originX = me.value.elementView.x
                var originY = me.value.elementView.y
                var originW = me.value.elementView.width
                var originH = me.value.elementView.height


                if (dx == null && dy == null) {
                    // resize

                    if (Math.abs(dl) <= Math.abs(dr)) {
                        // 오른쪽으로 움직임
                        if (dr >= 0) {
                            offsetX = originX + (Math.abs(dl + dr) / 2.0)
                        } else {
                            offsetX = originX - (Math.abs(dl + dr) / 2.0)
                        }


                    } else if (Math.abs(dl) > Math.abs(dr)) {
                        // 왼쪽 으로 움직임
                        if (dl >= 0) {
                            offsetX = originX - (Math.abs(dl + dr) / 2.0)
                        } else {
                            offsetX = originX + (Math.abs(dl + dr) / 2.0)
                        }

                    }

                    if (Math.abs(dlw) <= Math.abs(du)) {
                        //위로 움직임
                        if (du >= 0) {
                            offsetY = originY - (Math.abs(du + dlw) / 2.0)
                        } else {
                            offsetY = originY + (Math.abs(du + dlw) / 2.0)
                        }

                    } else if (Math.abs(dlw) > Math.abs(du)) {
                        //아래로 움직임
                        if (dlw >= 0) {
                            offsetY = originY + (Math.abs(du + dlw) / 2.0)
                        } else {
                            offsetY = originY - (Math.abs(du + dlw) / 2.0)
                        }

                    }

                    offsetW = dw
                    offsetH = dh

                } else if (dw == null && dh == null) {
                    //move
                    offsetX = originX + dx
                    offsetY = originY + dy
                    offsetW = originW
                    offsetH = originH
                } else {
                    console.log('error Move & Resize')
                }


                // if (designer.specVersion == '2.0' && (designer.parmType == 'mine' && designer.sharedCount > 0) || designer.parmType == 'share' && !designer.isRead) {
                if (designer.specVersion == '2.0' && !designer.isRead) {


                    me.movingElement = false

                    var projectId = localStorage.getItem('projectId') == null ? designer.parmProjectId : localStorage.getItem('projectId')
                    firebase.database()
                        .ref('definitions/' + projectId + '/queue')
                        .push({
                            state: 'elementMove',
                            editUid: localStorage.getItem('uid'),
                            elementId: me.value.elementView.id,
                            before: JSON.stringify({x: originX, y: originY, width: originW, height: originH}),
                            after: JSON.stringify({x: offsetX, y: offsetY, width: offsetW, height: offsetH}),
                            timeStamp: new Date().getTime(),
                        })
                } else {
                    alert('No Match Condition.')
                    window.location.reload(true);
                }

            },
            mergeDeep(target, sources) {
                // console.log(target)
                if (!sources.length) return target;
                const source = sources.shift();

                if (isObject(target) && isObject(source)) {
                    for (const key in source) {
                        if (isObject(source[key])) {
                            if (!target[key]) Object.assign(target, {[key]: {}});
                            mergeDeep(target[key], source[key]);
                        } else {
                            Object.assign(target, {[key]: source[key]});
                        }
                    }
                }

                return this.mergeDeep(target, sources);
            }
            ,
            onDeleted() {

            }
            ,
            deleteActivity() {
                var me = this
                var designer = me.getComponent('modeling-designer');


                if (designer.specVersion == '2.0' && designer.parmType != 'local') {
                    // designer.isReceiveQueue = true
                }

                me.$emit('update:value', null);
            }
            ,
            difference: _.debounce(
                function (object, base) {
                    function changes(object, base) {
                        return _.transform(object, function (result, value, key) {
                            if (!_.isEqual(value, base[key])) {
                                result[key] = (_.isObject(value) && _.isObject(base[key])) ? changes(value, base[key]) : value;
                            }
                        });
                    }

                    return changes(object, base);
                }, 100
            ),
            onLabelChanged:

                function (be, af) {
                    // console.log("onLabelChanged", be, af);
                }

            ,
            onRotateShape: function (me, angle) {
                this.value.elementView.angle = angle
            }
            ,
            connected: function () {
                // console.log(this.value)
            }
            ,

            onMoveShape: function () {
                console.log('onMoveShape')
            },
            selectedActivity: function () {
                if (this.value) {
                    this.selected = true
                }
            },

            deSelectedActivity: function () {
                var me = this

                if (me.value) {
                    me.selected = false
                    me.openPanel = false
                }
            },


            /**
             * 자신에게 도형들이 그룹으로 들어왔을때의 이벤트
             * @param groupElement
             * @param elements
             */
            onAddToGroup: function (groupElement, elements, eventOffset) {
                var me = this
                // console.log('onAddToGroup',groupElement, elements)


            }
            ,
            /**
             * 자신이 그룹속으로 들어갔을 때의 이벤트
             * @param groupElement
             * @param element
             */
            onAddedToGroup: function (groupElement, element, eventOffset) {

                var me = this


            }
            ,
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            }
            ,
            refresh() {
                var me = this
                me.value.elementView.x = me.value.elementView.x + 1
                me.$nextTick(function () {
                    me.value.elementView.width = me.tmpWidth
                    me.value.elementView.height = me.tmpHeight
                    me.rotateMove = true
                    me.value.elementView.x = me.value.elementView.x - 1
                    me.$nextTick(function () {
                        me.value.elementView.width = me.tmpWidth
                        me.value.elementView.height = me.tmpHeight
                    })
                })
            }
            ,
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            }
            ,
            onRemoveShape: function (value) {
                var me = this
                console.log("this is super!!!!")
            },

            isAttached: function (otherElement) {


                var me = this;
                var inner = {
                    x: me.value.elementView.x - me.value.elementView.width / 2,
                    y: me.value.elementView.y - me.value.elementView.height / 2,
                    width: me.value.elementView.width,
                    height: me.value.elementView.height
                }

                var outer = {
                    x: otherElement.elementView.x - otherElement.elementView.width / 2,
                    y: otherElement.elementView.y - otherElement.elementView.height / 2,
                    width: otherElement.elementView.width,
                    height: otherElement.elementView.height
                }


                return me._isAttached(outer, inner)
            },

            _isAttached(outer, inner) {
                if (
                    //왼쪽 상단 모서리에 걸린 경우
                    (outer.x < inner.x + inner.width &&
                        outer.y < inner.y + inner.height)

                    &&

                    //우측 하단 모서리에 걸린 경우
                    (inner.x < outer.x + outer.width &&
                        inner.y < outer.y + outer.height)

                    &&

                    //오른쪽 상단 모서리에 걸린 경우
                    (inner.x < outer.x + outer.width &&
                        outer.y < inner.y + inner.height)

                    &&

                    //왼쪽 하단 모서리에 걸린 경우
                    (outer.x < inner.x + inner.width &&
                        inner.y < outer.y + outer.height)
                ) return true;

                return false;
            },


        }
    }
</script>


