<template>
    <v-container fluid>

        <div v-if="labInfo">

            <v-col>
                <v-toolbar dense color='grey lighten-2'>
                    <v-app-bar-nav-icon></v-app-bar-nav-icon>

                    <b>{{labInfo.labName}}</b>

                    <v-spacer></v-spacer>


                    <v-dialog
                            v-model="missionDialog"
                            width="1000"
                            height="800"
                    >
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn
                                    color="green"
                                    dark
                                    v-bind="attrs"
                                    v-on="on"
                            >
                                미션 보기
                            </v-btn>
                        </template>

                        <v-card>
                            <v-card-title
                                    class="headline green lighten-2"
                                    primary-title
                            >
                                미션
                            </v-card-title>

                            <v-card-text>
                                <div class="py-3" v-html="labInfo.instructionHtml"></div>
                            </v-card-text>


                            <v-divider></v-divider>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                        color="primary"
                                        text
                                        @click="missionDialog = false"
                                >
                                    Close
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>


                    <v-dialog
                            v-model="theoryDialog"
                            :width="theoryExtended ? 1240:1000"
                            height="800"
                    >

                        <template v-slot:activator="{ on, attrs }">
                            <v-btn
                                    color="primary"
                                    text
                                    v-bind="attrs"
                                    v-on="on"
                            >
                                이론 보기
                            </v-btn>
                        </template>

                        <v-card>
                            <v-card-title
                                    class="headline grey lighten-2"
                                    primary-title
                            >
                                이론

                                <v-col cols="12" sm="3">

                                    <v-btn icon :color="theoryExtended ? 'indigo':'blue'"
                                           @click="theoryExtended=!theoryExtended">
                                        <v-icon>mdi-poll</v-icon>
                                    </v-btn>
                                </v-col>
                            </v-card-title>

                            <v-card-text>
                                <v-row>
                                    <v-col width=500>
                                        <div class="py-3" v-html="labInfo.theoryHtml"></div>
                                    </v-col>
                                    <v-col v-if="theoryExtended">
                                        <poll-result v-model="userMessages" @answered="onQuizAnswered"></poll-result>
                                    </v-col>

                                </v-row>

                            </v-card-text>


                            <v-divider></v-divider>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                        color="primary"
                                        text
                                        @click="theoryDialog = false"
                                >
                                    Close
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>


                    <v-dialog
                            v-model="answerDialog"
                            width="500"
                            height="300"
                    >

                        <template v-slot:activator="{ on, attrs }">
                            <v-btn
                                    color="primary"
                                    text
                                    v-bind="attrs"
                                    v-on="on"
                            >
                                응답 하기
                            </v-btn>
                        </template>

                        <v-card>
                            <v-card-title
                                    class="headline grey lighten-2"
                                    primary-title
                            >
                                응답
                            </v-card-title>

                            <v-card-text>

                                <answer :lab-info="labInfo" :class-info="classInfo"></answer>

                            </v-card-text>


                            <v-divider></v-divider>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                        color="primary"
                                        text
                                        @click="answerDialog = false"
                                >
                                    Close
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-btn text :to="'/courses/' + courseId + '/' + classId + '/' + labId + '/' + myId"
                           large color="primary" target="_blank">랩실 가기
                    </v-btn>
                    <v-btn text :to="'/courses/' + courseId + '/' + classId"
                           large color="primary">랩 목록 돌아가기
                    </v-btn>

                    <coin-drop @drop="deposit" v-model="scoreToBeDeposited"
                               v-if="isAdmin && labStatus.status=='started'"></coin-drop>

                    <v-btn color="green" dark v-if="isAdmin && labStatus.status=='READY'" @click="startLab">시작</v-btn>
                    <v-btn color="red" dark v-if="isAdmin && labStatus.status=='started'" @click="completeLab">종료
                    </v-btn>
                    <v-btn color="green" dark v-if="isAdmin && labStatus.status=='completed'" @click="startLab">재시작
                    </v-btn>

                </v-toolbar>
            </v-col>
        </div>

        <v-row v-if="classInfo">


            <v-subheader>Class Room Status</v-subheader>
            <v-spacer></v-spacer>
            <div c lass="ma-5" style="margin-right:20px; margin-top:0px; margin-bottom:0px">
                <Countdown v-if="deadline && labStatus && labStatus.status=='started'" :deadline="deadline"></Countdown>
            </div>
            <br>
        </v-row>
        <v-row v-if="classInfo">

            <v-col align="center" v-for="(group, groupIdx) in classInfo.groupedUsers" :key="groupIdx">
                <v-card width="350px" height="350px">
                    <v-card-title>
                        {{group.groupName}} ({{Math.round((group.passedMemberCount ? group.passedMemberCount: 0) *
                        100/group.users.length)}}%)
                    </v-card-title>
                    <v-card-text center>
                        <v-row align="center">
                            <v-col v-for="(user,userIdx) in group.users">
                                <v-badge
                                        :color="userMessages[user.email] ? (userMessages[user.email]=='헬프미' ? 'red':'grey'): 'white'"
                                        overlap
                                        right
                                        v-model="showMessage"
                                >

                                    <template v-slot:badge>
                                        <span slot="badge">{{userMessages[user.email] ? userMessages[user.email]: ''}}</span>
                                    </template>
                                    <template v-slot:badge>
                                        <span slot="badge">{{userMessages[user.email] ? userMessages[user.email]: ''}}</span>
                                    </template>

                                    <v-icon v-if="user.userImage == undefined" x-large @click="selectUser(user)"
                                            class="ma-0">
                                        mdi-account-circle
                                    </v-icon>
                                    <v-avatar v-else @click="selectUser(user)" class="ma-0">
                                        <img
                                                :src=user.userImage
                                        >
                                    </v-avatar>


                                </v-badge>


                                <br>

                                <div>
                                    <font v-if="userResults[user.email] && userResults[user.email].allPassed"
                                          color="green">
                                        <b>{{user.name}} <br> {{userResults[user.email] ?
                                            userResults[user.email].passMessage: ''}}</b>

                                    </font>
                                    <div v-else>
                                        {{user.name}} <br> {{userResults[user.email] ?
                                        userResults[user.email].passMessage: ''}}

                                        <v-skeleton-loader v-if="userResults[user.email]==null"
                                                           ref="skeleton"
                                                           type="sentences"
                                                           max-height="10"
                                        >
                                        </v-skeleton-loader>
                                    </div>
                                </div>

                                <v-chip
                                        class="ma-0"
                                        :color="userScores[user.email].score == bestScore ? 'orange': 'green'"
                                        text-color="white"

                                        v-if="showingScores && userScores[user.email]"
                                >
                                    {{userScores[user.email].score ? Math.round(userScores[user.email].score): '0'}}

                                    <v-icon v-if="userScores[user.email].score == bestScore" right>mdi-trophy</v-icon>
                                </v-chip>

                            </v-col>
                        </v-row>
                    </v-card-text>
                </v-card>
            </v-col>

            <v-col v-if="selectedUser">
                <v-navigation-drawer
                        v-model="selectedUser"
                        clipped
                        app
                        stateless
                        width="400"
                >
                    <div align="right" style="margin-top: 5px; margin-right: 5px; margin-bottom: 5px">
                        <v-icon text @click="unselectUser()">mdi-close</v-icon>
                    </div>
                    <v-divider></v-divider>
                    <v-icon x-large
                            v-bind:style="{ color: selectedUser.color }">
                        mdi-account-circle
                    </v-icon>
                    {{selectedUser.name}} - {{selectedUser.email}} -
                    <v-subheader> Hash Name: labs-{{ getHashCode() }}</v-subheader>

                    <v-list dense>
                        <v-subheader>
                            Checkpoints
                        </v-subheader>
                        <v-list-item v-for="(item,idx) in userResults[selectedUser.email]" :key="idx">
                            <v-list-item-content>
                                <v-list-item-title>{{idx + 1}}. {{item.text}}</v-list-item-title>
                            </v-list-item-content>
                            <v-list-item-action>
                                <v-checkbox
                                        v-if="!(item.javascript || item.regExp) && isAdmin"
                                        @click.stop="sendCheckPoints(selectedUser.email, item)"
                                        v-model="item.status"
                                        success
                                ></v-checkbox>
                                <v-checkbox
                                        v-if="(item.javascript || item.regExp) || !isAdmin"
                                        v-model="item.status"
                                        success
                                        readonly
                                ></v-checkbox>
                            </v-list-item-action>
                        </v-list-item>
                    </v-list>
                    <v-divider></v-divider>
                    <div style="margin-top: 10px;">
                        <v-btn :to="'/courses/' + courseId + '/' + classId + '/' + labId + '/' + selectedUser.email"
                               large color="primary" target="_blank" style="margin-left: 10px;">랩실 보기
                        </v-btn>
                        <v-btn v-if="(isAdmin || selectedUser.email == myId) && selectedUser.deleteInterval == undefined"
                               @click="clearLab(selectedUser)"
                               large color="error" target="_blank" style="margin-left: 30px;">랩실 삭제
                        </v-btn>
                        <v-btn v-else-if="isAdmin || selectedUser.email == myId"
                               large color="error" target="_blank" style="margin-left: 30px;">
                            <v-progress-circular color="primary"
                                                 indeterminate></v-progress-circular>
                        </v-btn>
                    </div>
                    <component v-if="tool.result"
                               :is="'lab-tool-' + labInfo.tool"
                               v-model="tool"
                               :lab-info="labInfo"
                               readonly
                               small
                    >
                    </component>

                    <v-skeleton-loader v-if="typeof tool.result === 'undefined'"
                                       ref="skeleton"
                                       type="card"
                                       class="mx-auto"
                    >
                    </v-skeleton-loader>
                </v-navigation-drawer>
            </v-col>
        </v-row>
        <v-row v-else>
            <v-col>
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
            </v-col>
        </v-row>
        <question v-if="isAdmin" :lab-info="labInfo" :class-info="classInfo"></question>


    </v-container>


</template>

<script>

    import LabBase from "./LabBase"
    import Answer from "./Answer"
    import UserNavigator from "./UserNavigator";
    import Question from "./Question";
    import LabToolKuberEz from "./tools/LabToolKuberEz"
    import LabToolEventStorming from "./tools/LabToolEventStorming"
    import LabToolIde from "./tools/LabToolIDE"
    import PollResult from "./PollResult"
    import firebase from 'firebase'
    //    import PieChart from 'v-easy-chart'
    import CoinDrop from "./CoinDrop"
    import Countdown from './Countdown'

    export default {
        name: "ClassRoom",
        components: {
            Question,
            UserNavigator,
            Answer,
            Question,
            PollResult,
            CoinDrop,
            Countdown,

            LabToolEventStorming,
            LabToolIde,
            LabToolKuberEz
        },
        mixins: [LabBase],
        data: () => ({
            classInfo: null,
            userResults: {},
            userScores: {},
            userMessages: {},
            labInfo: null,
            pollingJob: null,
            selectedUser: null,
            log: 'not loaded yet',
            show: true,
            missionSheet: false,
            tool: {result: null},
            chartData: [
                {value: 335, name: "istanbul"},
                {value: 310, name: "ankara"},
                {value: 234, name: "izmir"},
                {value: 135, name: "adana"},
                {value: 1548, name: "mersin"}
            ],
            theoryDialog: false,
            missionDialog: false,
            answerDialog: false,
            theoryExtended: false,
            showingScores: false,
            bestScore: 0,
            scoreToBeDeposited: 0,
            quizAnswer: null,
            labStatus: {
                status: null
            },
            deadline: null,
            deleteInterval: null
        }),

        created() {
            var me = this
        },
        computed: {
            showMessage() {
                return true;
                //return !this.showingScores
            }

        },
        watch: {},
        mounted() {
            this.init();
            window.document.title = '클래스룸'

        },
        destroyed() {
            clearInterval(this.pollingJob)
        },
        methods: {
            async init() {
                var me = this;
                this.loadLabInfo()
                await this.loadClassInfo();
                me.loadAllUserMessages();

                me.pollingJob = setInterval(function () {
                    me.loadAllUserResults()
                    me.loadToolResult()
                }, 5000)
                if (!this.classInfo.openClass)
                    me.setUserImages();
                await this.loadLabStatus()

                if (me.labStatus) {
                    var d = new Date();
                    d.setTime(me.labStatus.startedAt.getTime() + (me.labInfo.labTime ? me.labInfo.labTime : 10) * 60 * 60 * 1000)
                    me.deadline = d.toString()
                }
            },
            setUserImages() {
                var me = this
                this.classInfo.groupedUsers.forEach(function (group, groupIdx) {
                    group.users.forEach(async function (user, userIdx) {
                        var userInfo = await me.getString(`${me.courseId}/runningClass/${me.classId}/enrolledUsers/${user.email}.txt`)
                        user.userImage = userInfo.photoURL;
                    })
                })
            },
            getHashCode() {
                var me = this
                var course = me.courseId;
                var clazz = me.classId;
                var lab = me.labId;
                var userEmail = me.selectedUser.email;
                var filePath = course + '/runningClass/' + clazz + '/labs/' + lab + '/' + userEmail;
                return me.hashCode(filePath)
            },
            async loadAllUserResults() {
                var me = this
                this.classInfo.groupedUsers.forEach(function (group, groupIdx) {
                    group.users.forEach(function (user, userIdx) {
                        me.loadUserResult(user.email)

//                        me.loadMessage(user.email)
                    })
                })

                me.userResults.__ob__.dep.notify()

            },

            async selectUser(user) {
                this.selectedUser = user;
                this.tool.result = undefined;
            },
            unselectUser() {
                this.selectedUser = null;
            },
            async loadLabInfo() {
                this.labInfo = await this.getLabInfo();
            },

            async loadLabStatus() {
                var me = this;

                this.labStatus = await me.getLabStatus(this.labId);

                var scores = await this.getObject(`${me.courseId}/runningClass/${me.classId}/userScores.json`);
                if (scores) {
                    this.userScores = scores;
                    this.showScore();
                }

            },

            async loadToolResult() {
                var me = this
                if (me.selectedUser)
                    try {

                        var result = await me.getObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.selectedUser.email}/toolResult.json`)
                        this.tool.result = result
                        this.tool.__ob__.dep.notify()
                    } catch (e) {
                        console.log(e)
                    }
            },
            async loadAllUserMessages() {
                var me = this;
                var starCountRef = firebase.database().ref('labs/' + `${me.courseId}/runningClass/${me.classId}/${me.labId}/messages/`);
                starCountRef.on('value', function (snapshot) {
                    console.log(snapshot.val())
                    var keyLists = Object.keys(snapshot.val());
                    console.log(keyLists)
                    keyLists.forEach(function (key) {
                        me.userMessages[snapshot.val()[key].email] = snapshot.val()[key].message
                    })
                    me.userMessages[snapshot.val().email] = snapshot.val().message;
                });
                me.userMessages = {}; //clear all before get it
                me.userMessages.__ob__.dep.notify()
            },

            async loadClassInfo() {
                this.classInfo = await this.getClassInfo();
                this.classInfo.__ob__.dep.notify();
            },

            async loadUserResult(userId) {
                var me = this
                try {
                    var userResult = await this.getObject(
                        `${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${userId}/checkpointResults.json`
                    );
                    if (userResult == null && userId == me.myId) {
                        me.putObject(`${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${me.userId}/checkpointResults.json`, me.labInfo.checkPoints)
                        userResult = me.labInfo.checkPoints
                    }
                    this.userResults[userId] = userResult;

                    var passInfo = this.getPassInfo(userId);
                    if (!passInfo)
                        this.userResults[userId].passMessage = "N/A";
                    else {
                        this.userResults[userId].passMessage = passInfo.passed + "/" + passInfo.all;
                        this.userResults[userId].passInfo = passInfo;
                        this.userResults[userId].allPassed = (passInfo.passed == passInfo.all);
                    }

                    this.calculateGroupStatus();

                } catch (e) {

                    console.log(e)
                    // if (e.code == "NoSuchKey") {
                    //     // 만약 파일이 없는 에러일 경우 랩 info의 파일을 올려준다.
                    //     await me.putObject(`${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${userId}/checkpointResults.json`, me.labInfo.checkPoints)
                    // }
                }
            },
            calculateGroupStatus() {
                var me = this;
                this.classInfo.groupedUsers.forEach(group => {
                    var passedMemberCount = 0;
                    group.users.forEach(user => {
                        if (me.userResults[user.email] && me.userResults[user.email].allPassed)
                            passedMemberCount++;
                    })

                    group.passedMemberCount = passedMemberCount;
                });

                this.classInfo.groupedUsers.__ob__.dep.notify();

            },
            async sendCheckPoints(email, item) {
                var me = this
                item.status = !item.status;
                var tmpArray = []
                me.userResults[email].forEach(function (item) {
                    tmpArray.push(item)
                })
                this.putObject(
                    `${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${email}/checkpointResults.json`, tmpArray
                );
            },
            getPassInfo(userId) {
                var checkpoints = this.userResults[userId];
                if (!checkpoints)
                    return null;
                else {
                    var passed = 0;
                    for (var i = 0; i < checkpoints.length; i++) {
                        if (checkpoints[i].status) passed++;
                    }
                    return {
                        passed: passed,
                        all: checkpoints.length
                    };
                }

            },
            showScore() {
                var me = this;
                if (!me.showingScores) {
                    me.showingScores = true;

                    setTimeout(() => {
                        me.showingScores = false;
                        me.quizAnswer = null;
                    }, 20000)
                }
            },

            deposit(score) {
                var me = this;

                this.showScore();

                Object.keys(this.userResults).forEach(function (email) {
                    if (me.userResults[email] && me.userResults[email].passInfo &&
                        ((!me.quizAnswer && me.userResults[email].passInfo.passed > 0) || (me.quizAnswer && me.userMessages[email] == me.quizAnswer))
                    ) {

                        if (!me.userScores[email])
                            me.userScores[email] = {};

                        if (!me.userScores[email].score)
                            me.userScores[email].score = 0;

                        var partialScore = me.quizAnswer ? 1 : me.userResults[email].passInfo.passed / me.userResults[email].passInfo.all

                        me.userScores[email].score += partialScore;

                        if (me.userScores[email].score > me.bestScore)
                            me.bestScore = me.userScores[email].score;
                    }
                })

                me.userScores.__ob__.dep.notify()

            },

            onQuizAnswered(answer) {
                this.quizAnswer = answer;
                // var addingScore = parseInt(prompt("추가 점수"));
                // this.scoreToBeDeposited = addingScore;

            },
            async clearLab(selectUser) {
                var me = this
                var hashName = 'labs-' + this.getHashCode()
                this.$http.delete(`http://34.84.88.143/api/v1/namespaces/default/pods/${hashName}`);
                this.$http.delete(`http://34.84.88.143/api/v1/namespaces/default/services/${hashName}`);
                selectUser.deleteInterval = setInterval(
                    function () {
                        me.$http.get(`http://34.84.88.143/api/v1/namespaces/default/pods/${hashName}/status`).then(function () {
                        }).catch(function (e) {
                            // 에러가 나면 없는 pod 결과가 경우임. 따라서 완전히 제거된 상태
                            clearInterval(selectUser.deleteInterval)
                            selectUser.deleteInterval = undefined
                        })
                    }, 2000)
                var filePath = this.courseId + '/runningClass/' + this.classId + '/labs/' + this.labId + '/' + this.selectedUser.email;
                var templateFile = me.labInfo.templateFile;
                var fileList = await this.getListRecursive(filePath + '/' + templateFile);
                console.log(fileList)
                fileList.forEach(function (item) {
                    me.delete(item.name)
                })
                // removeObject 안됨.
                // this.$minioClient.removeObjects('labs', fileList, function(e) {
                //     if (e) {
                //         return console.log('Unable to remove Objects ',e)
                //     }
                //     selectUser.labDeleting = false;
                //     console.log('Removed the objects successfully')
                // })
                // this.delete(filePath + '/' + templateFile + '/');
            },
            getListRecursive(path, metadata) {
                var me = this;
                var listObj = [];

                return new Promise((resolve, reject) => {
                    this.$minioClient.listObjects('labs', path, true)
                        .on('data', function (obj) {
                            if (obj.name) {
                                listObj.push(obj)
                            }
                        })
                        .on('error', function (err) {
                            reject(err);
                        })
                        .on('end', function (err) {
                            resolve(listObj)
                        })
                });
            },
            async startLab() {
                var me = this;

                await this.putString(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/status/started`, '')
                this.labStatus.status = 'started'
                this.labStatus.startedAt = new Date()

            },

            async completeLab() {
                var me = this;
                await this.putString(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/status/completed`, '')
                await this.putObject(`${me.courseId}/runningClass/${me.classId}/userScores.json`, this.userScores)
                this.labStatus.status = 'completed'
                this.labStatus.completedAt = new Date();
            }


        }
    };
</script>
<style>

</style>